.main-catalog {
  display: block;
  padding-top: 15px;

  @include side-padding;

  @media (min-width: $breakpoint-tablet) {
    padding-top: 32px;
  }

  @media (min-width: $breakpoint-desktop) {
    padding-top: 46px;
  }

  h1 {
    margin: 0;
    margin-bottom: 28px;

    font-size: 36px;
    line-height: 1.67;
    color: $color-title;

    @include font-default;

    @media (min-width: $breakpoint-tablet) {
      margin-bottom: 25px;

      font-size: 60px;
      line-height: 1;
    }

    @media (min-width: $breakpoint-desktop) {
      margin-bottom: 23px;
    }
  }
}
