.contact {
  @media (min-width: $breakpoint-desktop) {
    position: relative;
  }
}

.contact__info {
  display: flex;
  padding-top: 28px;
  padding-bottom: 26px;

  @include side-padding;

  @media (min-width: $breakpoint-tablet) {
    justify-content: space-between;
    padding: 65px 70px 59px;
  }

  @media (min-width: $breakpoint-desktop) {
    position: absolute;
    top: 95px;
    left: 40px;
    z-index: 50;

    width: 570px;
    min-height: 201px;

    background-color: $color-bg;
  }
}

.contact__invitation {
  width: 50%;
  margin: 0;

  font-size: 16px;
  line-height: 20px;
  color: $color-title;
  text-transform: uppercase;

  @include font-default;

  @media (min-width: $breakpoint-tablet) {
    width: 154px;

    font-size: 20px;
    line-height: 25px;
  }
}

.contact__address {
  width: 50%;
  margin: 0;

  font-size: 14px;
  line-height: 20px;
  color: $color-content;

  @include font-alt;

  @media (min-width: $breakpoint-tablet) {
    width: 157px;

    font-size: 16px;
    line-height: 20px;
    text-align: right;
  }

  span {
    @media (min-width: $breakpoint-tablet) {
      display: block;
      margin-top: 18px;
    }
  }
}
